.cover-photo-container {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center; }
  .cover-photo-container.big {
    height: 100vh; }
  .cover-photo-container.small {
    height: 50vh; }
  .cover-photo-container .photo {
    height: 100%;
    width: 100%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover; }
  .cover-photo-container .overlay {
    background-color: rgba(0, 0, 0, 0.5);
    height: 100%;
    width: 100%;
    position: absolute; }
  .cover-photo-container .content {
    display: flex;
    flex-direction: column;
    position: absolute; }
    .cover-photo-container .content .title {
      font-size: 60px;
      color: white;
      font-family: 'bromello', 'Shink';
      text-align: center;
      z-index: 2;
      line-height: 1.3em;
      letter-spacing: 3px; }
    .cover-photo-container .content .subtitle {
      font-size: 12px;
      color: white;
      font-family: 'Tahoma', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
      text-align: center;
      z-index: 2;
      letter-spacing: 10px;
      text-transform: uppercase; }
    @media (min-width: 992px) {
      .cover-photo-container .content .title {
        font-size: 90px; }
      .cover-photo-container .content .subtitle {
        font-size: 18px; } }

.btn-primary {
  background-color: #fff;
  border-color: #b58700;
  color: #b58700;
  padding: 8px 35px; }
  .btn-primary:hover {
    background-color: #b58700;
    border-color: #b58700;
    color: #fff; }
  .btn-primary:not(:disabled):not(.disabled):active {
    background-color: #b58700;
    border-color: #b58700;
    color: #fff; }
  .btn-primary:focus {
    box-shadow: 0 0 0 0.2rem rgba(139, 104, 0, 0.5); }

.nav-wrapper {
  margin: 0;
  -webkit-box-shadow: 0px 3px 25px -16px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 3px 25px -16px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 3px 25px -16px rgba(0, 0, 0, 0.75); }
  .nav-wrapper ul {
    display: flex;
    flex-wrap: wrap;
    padding-left: 0;
    margin-bottom: 0;
    list-style: none; }
    .nav-wrapper ul li {
      background-color: white;
      flex-basis: 0;
      flex-grow: 1;
      text-align: center;
      display: flex;
      justify-content: center;
      align-items: center; }
      .nav-wrapper ul li.active {
        border-bottom: #b58700 5px solid;
        height: 40px; }
      .nav-wrapper ul li:hover {
        background-color: #e9ecef; }
      .nav-wrapper ul li a {
        font-size: 12px;
        font-weight: 500;
        text-transform: uppercase;
        letter-spacing: 2px;
        display: block;
        padding: 0.5rem 1rem;
        text-decoration: none;
        line-height: normal; }
  @media (min-width: 992px) {
    .nav-wrapper {
      margin: -30px 100px 0; }
      .nav-wrapper ul li a {
        line-height: 2.5em;
        font-size: 14px;
        width: 100%;
        height: 100%; } }

a {
  color: #b58700; }
  a:hover {
    color: #8b6800; }

.content-page {
  background-image: url(https://i.pinimg.com/originals/0d/40/6b/0d406bf054e7332af25b1127b6cf9764.jpg);
  background-size: cover;
  padding: 50px 20px;
