      .content-page .content form .form-group .custom-radio .custom-control-input {
        /* This is the default state */
        /* active state i.e. displayed while the mouse is being pressed down */
        /* the shadow; displayed while the element is in focus */ }
        .content-page .content form .form-group .custom-radio .custom-control-input:checked ~ .custom-control-label::before {
          background-color: #b58700;
          border-color: #b58700; }
        .content-page .content form .form-group .custom-radio .custom-control-input:active ~ .custom-control-label::before {
